@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;700&display=swap");
@import url("https://fonts.googleapis.com/icon?family=Material+Icons");

body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --font-inter: Inter;

  /* font sizes */
  --font-size-mini: 15px;
  --font-size-3xs: 10px;
  --font-size-xl: 20px;

  /* Colors */
  --color-darkgray: #9d9d9d;
  --color-gray-100: #7b7b7b;
  --color-gray-200: #050505;
  --color-white: #fff;
  --color-black: #000;

  /* border radiuses */
  --br-8xs: 5px;
}
