.loginChild,
.loginIcon,
.loginItem {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
}
.loginChild,
.loginItem {
  height: 46.19%;
  width: 58.06%;
  top: 25.98%;
  right: 20.63%;
  bottom: 27.83%;
  left: 21.32%;
}
.loginItem {
  width: 25.42%;
  right: 53.26%;
}
.signIn {
  position: absolute;
  top: 35.45%;
  left: 53.06%;
}
.legalManagementSystem {
  position: absolute;
  top: 72.17%;
  left: 41.74%;
  font-size: var(--font-size-mini);
  color: var(--color-darkgray);
}
.forgottenPassword {
  text-decoration: none;
  position: absolute;
  top: 57.81%;
  left: 66.81%;
  font-size: var(--font-size-3xs);
  font-weight: 700;
  color: #4a4a4a;
  cursor: pointer;
}
.email,
.password {
  border: 0;
  background-color: transparent;
  position: absolute;
  height: 4%;
  width: 21.25%;
  top: 41.7%;
  right: 25.69%;
  bottom: 54.3%;
  left: 53.06%;
}
.password {
  top: 49.61%;
  bottom: 46.39%;
}
.submit {
  position: relative;
  font-size: var(--font-size-3xs);
  font-family: var(--font-inter);
  color: var(--color-white);
  text-align: left;
}
.image1Icon,
.send {
  position: absolute;
  overflow: hidden;
}
.send {
  cursor: pointer;
  border: 0;
  padding: 0;
  background-color: var(--color-gray-200);
  width: 6.11%;
  top: calc(50% + 70px);
  right: 40.56%;
  left: 53.33%;
  border-radius: var(--br-8xs);
  height: 30px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.send:hover {
  background-color: var(--color-gray-100);
}
.image1Icon {
  height: 24.61%;
  width: 24.72%;
  top: 38.09%;
  right: 54.65%;
  bottom: 37.3%;
  left: 20.63%;
  max-width: 100%;
  max-height: 100%;
  object-fit: cover;
}
.login,
.login1 {
  position: relative;
  width: 1440px;
  height: 1024px;
}
.login {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  text-align: left;
  font-size: var(--font-size-xl);
  color: var(--color-black);
  font-family: var(--font-inter);
}
